lor-secondary-foreground-1-disabled);\n}\n\n/* CTA */\n.btn-cta.btn-filled .btn-icon {\n  color: var(--semantic-color-cta-oncolor-global);\n}\n\n.btn-cta.btn-outlined .btn-icon,\n.btn-cta.btn-transparent .btn-icon {\n  color: var(--semantic-color-cta-foreground-1-rest);\n}\n\n.btn-cta.btn-outlined:hover:not(:disabled) .btn-icon,\n.btn-cta.btn-transparent:hover:not(:disabled) .btn-icon {\n  color: var(--semantic-color-cta-foreground-1-hovered);\n}\n\n.btn-cta.btn-outlined:active:not(:disabled) .btn-icon,\n.btn-cta.btn-transparent:active:not(:disabled) .btn-icon {\n  color: var(--semantic-color-cta-foreground-1-pressed);\n}\n\n.btn-cta.btn-outlined:disabled .btn-icon,\n.btn-cta.btn-transparent:disabled .btn-icon {\n  color: var(--semantic-color-cta-foreground-1-disabled);\n}\n\n/* 아이콘 크기별 크기 조정 */\n.btn-icon-only.btn-xs {\n  padding: var(--semantic-spacingHor-global-xs);\n}\n\n.btn-icon-only.btn-sm {\n  padding: var(--semantic-spacingHor-global-sm);\n}\n\n.btn-icon-only.btn-md {\n  padding: var(--semantic-spacingHor-global-sm);\n}\n\n.btn-icon-only.btn-lg {\n  padding: var(--semantic-spacingHor-global-md);\n}\n\n.btn-icon-only.btn-xl {\n  padding: var(--semantic-spacingHor-global-md);\n}\n\n/* 추가 레이아웃 스타일 */\n.btn-full-width {\n  width: 100%;\n} "],"sourceRoot":""}]);
